.recepy__item {
    display: grid;
    grid-template-rows: -webkit-min-content;
    grid-template-rows: min-content;
    grid-template-columns: 1fr 2fr;
    grid-template-areas: "glass title";
    color: var(--text-color);
    font-size: 2rem;
    background-color: var(--card-color);
    padding: var(--padding);
    margin: 0.5rem;
    cursor: pointer;
}

.recepy__item__title {
    grid-area: title;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    /* justify-content: center; */
}

.recepy__item__glass {
    grid-area: glass;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.recepy__item:hover {
    background-color: var(--card-color-hover);
}


